import "./styles.css";
import { useEffect, useState } from "react";
import humanizeDuration from "humanize-duration";
import { Button, Card, CardBody, Col, Container, Row } from "reactstrap";
import LoadingSpinner from "./LoadingSpinner";
const apiProxy = "https://salty-ravine-55027.herokuapp.com/https://neos.com/";
const htmlParser = new DOMParser();
const updateInterval = 40000;

export default function App() {
  const [icoData, setIcoData] = useState([]);
  const [priceHist, setPriceHist] = useState([]);
  const [time, setTime] = useState(Date.now());
  const [lastUpdate, setLastUpdate] = useState(Date.now());
  const [isAlertAllowed, setIsAlertAllowed] = useState(false);
  const [isUpdating, setIsUpdating] = useState(false);
  const notifyUser = (msg) => {
    if (!("Notification" in window)) {
      alert("This browser does not support desktop notifications");
    } else if (Notification.permission === "granted") {
      setIsAlertAllowed(true);
      new Notification(msg);
    } else if (Notification.permission !== "denied") {
      Notification.requestPermission().then((permission) => {
        if (permission === "granted") {
          new Notification(msg);
        }
      });
    }
  };
  const updatePrice = async () => {
    setIsUpdating(true);
    const neosResp = await fetch(apiProxy);
    const neosHtml = await neosResp.text();
    const doc = htmlParser.parseFromString(neosHtml, "text/html");
    const icoSect = doc.getElementById("ico");
    const innerIco = icoSect.querySelector(".row > div > .row");
    const data = [...innerIco.querySelectorAll("article")].reduce(
      (mapArr, article) => {
        if (article?.childElementCount !== 2) return mapArr;
        const articleData = {
          header: article.firstElementChild.textContent.trim(),
          content: article.lastElementChild.textContent.trim()
        };
        if (articleData.header === "NCR minting price:") {
          document.title = articleData.content;
        }
        if (
          !mapArr[mapArr.length - 1] ||
          mapArr[mapArr.length - 1].length > 1
        ) {
          mapArr.push([articleData]);
        } else {
          mapArr[mapArr.length - 1].push(articleData);
        }
        return mapArr;
      },
      []
    );
    console.log(data);
    if (icoData.length && icoData[0].content !== data[0]?.content) {
      setPriceHist((prevState) => [...prevState, data]);
    }
    setIcoData(data);
    setLastUpdate(Date.now());
    setIsUpdating(false);
    setTimeout(updatePrice, updateInterval);
  };
  const tick = () => setTime(Date.now());
  useEffect(() => {
    updatePrice();
    setInterval(tick, 1000);
  }, []);
  return (
    <div className="App">
      <Container className="pb-5 oxy-light">
        <Row className="text-center">
          <Col md="12">
            <Card className="mt-5 p-3 text-light shadow main-card">
              <CardBody>
                <LoadingSpinner isAbsolute isEnabled={isUpdating} />
                {!!icoData.length &&
                  icoData.map((row, rowIndex) => (
                    <Row key={rowIndex} className="ico-row">
                      {row.map((category, index) => (
                        <Col key={index} className="md-6 sm-12">
                          <h5>{category.header}</h5>
                          <p>{category.content}</p>
                        </Col>
                      ))}
                    </Row>
                  ))}
                {!!icoData.length && (
                  <p className="text-muted mt-2">
                    Last updated
                    <span
                      style={{
                        display: "inline-block",
                        minWidth: "75px",
                        margin: "0 4px"
                      }}
                    >
                      {humanizeDuration(time - lastUpdate, { round: true })}
                    </span>
                    ago
                  </p>
                )}
                {!!priceHist.length ? (
                  <div>
                    Price history since page load:
                    {priceHist.map((prevPrice, index) => (
                      <p key={index}>{prevPrice}</p>
                    ))}
                  </div>
                ) : (
                  !!icoData.length && "Price has not changed since page load"
                )}
                {!!icoData.length && !isAlertAllowed && (
                  <div className="mt-5">
                    <Button
                      size="md"
                      className="oxy-light"
                      onClick={() => {
                        notifyUser(
                          "You'll be notified like this when NCR mint price changes."
                        );
                      }}
                    >
                      Enable alerts
                    </Button>
                  </div>
                )}
              </CardBody>
            </Card>
          </Col>
        </Row>
      </Container>
      <footer className="text-center text-muted mt-5 oxy-light">
        <p>WallTack#0001</p>
      </footer>
    </div>
  );
}
